@import "../ColorPallete.css"; /* Color Pallete and Font Family */

#aboutH1{
    color: var(--Grey);
    font-family: 'roboto', sans-serif;
    font-weight: 300;
    font-size: xx-large;
    margin-left: 24px;
    margin-top: 1em;
    margin-bottom: 1em;
}

#bodyBackground{
    background-color: var(--backgroundWhite);
}

#MontoyaH1{
    color: var(--BlueGreen);
    font-family: 'roboto', sans-serif;
    font-weight: 700;
    font-size: xx-large;
    padding-top: 12px;
    margin-bottom: 12px;
    margin-left: 24px;
}

#paragraphs{
    font-family: 'roboto', sans-serif;
    font-weight: 300;
    font-size: medium;
    margin-left: 24px;
    margin-right: 24px;
    padding-bottom: 12px;
}

/*Scale to Small Screens */
@media only screen and (max-width: 768px) {
    #aboutH1{
        font-size: x-large;
    }
    #MontoyaH1{
        font-size: x-large;
    }
}
@media only screen and (max-width: 640px) {
    #paragraphs{
        font-size: small;
    }
}
@media only screen and (max-width: 480px) {
    #aboutH1{
        font-size: large;
    }
    #MontoyaH1{
        font-size: large;
    }
    #paragraphs{
        font-size: small;
    }
}


