@import "./ColorPallete.css"; /* Color Pallete and Font Family */

#ProductCard{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    cursor: pointer;
    border-radius: 5px;
    width: 250px;
    aspect-ratio: 4 / 3;
    box-shadow: 0px 0px 6px 2px rgba(0,0,0,0.22);
    transition: 0.3s;
}
#ProductCard:hover{
    box-shadow: 0px 0px 12px 6px rgba(0,0,0,0.22);
}

#ProductCardTop{
    /* box-sizing: border-box; */
    padding: 10px;
    height: 20%;
    width: auto;
    font-size: small;
    font-family: 'roboto', sans-serif;
    font-weight: 300;
    text-align: justify;
    color: white;
    background-color: var(--BlueGreen);
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;

}

#ProductCardBody{
    padding: 10px;
    width: auto;
    display: flex;
    justify-content: center;
}

#ProductCardBody img{
    width: 95%;
    aspect-ratio: 4 / 3;
    border-radius: 3px;
}

/* Product Card Scale to Small Screens */
@media only screen and (max-width: 768px) {
    #ProductCard {
      width: 200px;
    }
    #ProductCardTop{
        font-size: x-small;
    }
}
@media only screen and (max-width: 640px) {
    #ProductCard {
      width: 150px;
    }
}
@media only screen and (max-width: 480px) {
    #ProductCard {
      width: 100px;
    }
}